html, body {
	-ms-overflow-style: none;
	overflow-style: unset;
	overflow-y: hidden;
}

html {

	&.nsr-is-webkit::-webkit-scrollbar {
		display: none;
	}

	&.nsr-is-moz {
		overflow-y: initial;
		scrollbar-width: none;
	}

	&.nsr-is-edge {
		-ms-overflow-style: none;
		-ms-overflow-y: initial;

		iframe {
			-ms-overflow-style: none;
			-ms-overflow-y: initial;
		}
	}

	&.nsr-is-msie {
		-ms-overflow-style: none;
		-ms-overflow-y: initial !important;
	}
}
